const generateAndSendInvitations = async () => {
  try {
    loading.value = true
    
    console.log('üîç [DEBUG] Starting invitation generation process...')
    
    // Get selected vendors from localStorage (set by Phase 3)
    const selectedVendorsData = localStorage.getItem('selectedVendors')
    const selectedRFPData = localStorage.getItem('selectedRFP')
    
    console.log('üìã [DEBUG] Raw localStorage data:', {
      selectedVendorsData,
      selectedRFPData
    })
    
    if (!selectedVendorsData || !selectedRFPData) {
      console.error('‚ùå [DEBUG] Missing vendor or RFP data in localStorage')
      showErrorToast("No vendor selection found", "Please go back to Phase 3 and select vendors first.")
      return
    }
    
    const allVendors = JSON.parse(selectedVendorsData)
    const rfpData = JSON.parse(selectedRFPData)
    
    console.log('üìä [DEBUG] Parsed data:', {
      allVendors,
      rfpData
    })
    
    // Update RFP ID from the stored data
    rfpId.value = rfpData.rfp_id
    
    // Prepare vendor data for invitations using the confirmed contact details
    const selectedVendors = []
    
    if (primaryContacts.value && primaryContacts.value.length > 0) {
      console.log('üë• [DEBUG] Using confirmed contact details:', primaryContacts.value)
      
      primaryContacts.value.forEach(contact => {
        if (contact.error) {
          console.warn('‚ö†Ô∏è [DEBUG] Skipping contact with error:', contact.error)
          return
        }
        
        console.log('üë§ [DEBUG] Processing contact:', contact)
        
        selectedVendors.push({
          vendor_id: contact.vendor_type === 'existing' ? contact.vendor_id : null,
          vendor_name: contact.full_name,
          email: contact.email,
          phone: contact.phone || contact.mobile,
          company_name: contact.company_name,
          is_matched_vendor: contact.vendor_type === 'existing',
          vendor_type: contact.vendor_type,
          contact_id: contact.contact_id
        })
      })
    } else {
      // Fallback to original logic if no contacts confirmed
      console.log('‚ö†Ô∏è [DEBUG] No confirmed contacts, using fallback logic')
      
      // Add existing vendors (matched vendors)
      if (allVendors.existing && allVendors.existing.length > 0) {
        console.log('üè¢ [DEBUG] Processing existing vendors:', allVendors.existing)
        
        allVendors.existing.forEach(vendor => {
          console.log('üë§ [DEBUG] Processing existing vendor:', vendor)
          
          selectedVendors.push({
            vendor_id: vendor.vendor_id,
            vendor_name: vendor.name || 'Primary Contact',
            email: vendor.email,
            phone: vendor.phone,
            company_name: vendor.company_name,
            is_matched_vendor: true,
            vendor_type: 'existing'
          })
        })
      }
      
      // Add manual vendors (unmatched vendors)
      if (allVendors.manual && allVendors.manual.length > 0) {
        console.log('üìù [DEBUG] Processing manual vendors:', allVendors.manual)
        
        allVendors.manual.forEach(vendor => {
          console.log('üë§ [DEBUG] Processing manual vendor:', vendor)
          
          selectedVendors.push({
            vendor_id: null, // No vendor_id for unmatched vendors
            name: vendor.vendor_name,
            email: vendor.vendor_email,
            phone: vendor.vendor_phone,
            company_name: vendor.company_name,
            is_matched_vendor: false,
            vendor_type: 'manual'
          })
        })
      }
      
      // Add bulk vendors (unmatched vendors)
      if (allVendors.bulk && allVendors.bulk.length > 0) {
        console.log('üì¶ [DEBUG] Processing bulk vendors:', allVendors.bulk)
        
        allVendors.bulk.forEach(vendor => {
          console.log('üë§ [DEBUG] Processing bulk vendor:', vendor)
          
          selectedVendors.push({
            vendor_id: null, // No vendor_id for unmatched vendors
            name: vendor.vendor_name,
            email: vendor.vendor_email,
            phone: vendor.vendor_phone,
            company_name: vendor.company_name,
            is_matched_vendor: false,
            vendor_type: 'bulk'
          })
        })
      }
    }
    
    if (selectedVendors.length === 0) {
      showErrorToast("No vendors to invite", "Please select vendors in Phase 3 first.")
      return
    }
    
    console.log('üì§ [DEBUG] Final selectedVendors array:', selectedVendors)
    console.log('üì§ [DEBUG] About to create invitations for vendors:', selectedVendors)
    
    // Create invitations with unique URLs
    const response = await vendorInvitationService.createInvitations(
      rfpId.value,
      selectedVendors,
      customMessage.value || "Please review our RFP and submit your proposal.",
      { 
        source: "rfp_portal", 
        campaign: "vendor_invitation",
        includeDeadline: includeDeadline.value,
        customMessage: customMessage.value
      }
    )
    
    console.log('‚úÖ [DEBUG] Invitation creation response:', response)
    
    // Send email invitations
    if (response.invitations && response.invitations.length > 0) {
      console.log('üìß [DEBUG] Sending emails for invitations:', response.invitations)
      
      // Map the invitations to the format expected by sendInvitations
      const invitationsToSend = response.invitations.map(inv => ({
        invitation_id: inv.invitation_id, // Use the actual numeric invitation_id from database
        vendor_email: inv.vendor_email,
        vendor_name: inv.vendor_name,
        invitation_url: inv.invitation_url,
        company_name: inv.company_name,
        unique_token: inv.unique_token,
        custom_message: inv.custom_message
      }))
      
      console.log('üì§ [DEBUG] Mapped invitations for sending:', invitationsToSend)
      
      const sendResponse = await vendorInvitationService.sendInvitations(rfpId.value, invitationsToSend, rfpData)
      console.log('‚úÖ [DEBUG] Send invitations response:', sendResponse)
      
      // Check if any invitations need manual sending
      if (sendResponse.results) {
        const manualInvitations = sendResponse.results.filter(r => r.success && !r.email_sent)
        const failedInvitations = sendResponse.results.filter(r => !r.success)
        
        if (failedInvitations.length > 0) {
          console.warn('‚ö†Ô∏è [DEBUG] Some invitations failed:', failedInvitations)
          showErrorToast(`Some invitations failed: ${failedInvitations.map(f => f.error).join(', ')}`)
        }
        
        if (manualInvitations.length > 0) {
          console.log('üìß [DEBUG] Manual sending required:', manualInvitations)
          showToast("Invitations Created", `${manualInvitations.length} invitation(s) created successfully. Email sending is disabled - please send the invitation URLs manually.`, "info")
          
          // Store manual invitations for display
          manualInvitations.forEach(inv => {
            console.log(`üìß Manual Email for ${inv.vendor_name} (${inv.vendor_email}):`)
            console.log(`üîó URL: ${inv.invitation_url}`)
            console.log(`üìù Content: ${inv.email_content}`)
          })
        }
      }
    }
    
    showToast("Invitations Sent", `Generated unique URLs and sent ${selectedVendors.length} vendor invitation(s) successfully.`)
    await loadInvitations()
    await loadInvitationStats()
    await loadRecentActivity()
  } catch (error) {
    console.error('Error creating and sending invitations:', error)
    showErrorToast("Failed to create and send invitations")
  } finally {
    loading.value = false
  }
}

const resendAllInvitations = async () => {
  try {
    loading.value = true
    const pendingInvitations = invitedVendors.value.filter(v => v.invitation_status === 'SENT')
    
    if (pendingInvitations.length === 0) {
      showToast("No pending invitations to resend")
      return
    }
    
    const invitationIds = pendingInvitations.map(v => v.invitation_id)
    
    // Get RFP data from localStorage
    const selectedRFPData = localStorage.getItem('selectedRFP')
    let rfpData = {
      rfp_title: "Sample RFP Title",
      rfp_number: "RFP-2024-001",
      deadline: "2025-02-15"
    }
    
    if (selectedRFPData) {
      try {
        const parsedRfpData = JSON.parse(selectedRFPData)
        rfpData = {
          rfp_title: parsedRfpData.rfp_title || rfpData.rfp_title,
          rfp_number: parsedRfpData.rfp_number || rfpData.rfp_number,
          deadline: parsedRfpData.deadline || rfpData.deadline
        }
      } catch (error) {
        console.error('‚ùå [DEBUG] Error parsing RFP data:', error)
      }
    }
    
    // Convert invitation IDs to invitation objects for resending
    const invitationObjects = pendingInvitations.map(inv => ({
      invitation_id: inv.invitation_id,
      vendor_email: inv.vendor_email || "contact@example.com",
      vendor_name: inv.vendor_name || "Vendor Contact", 
      invitation_url: inv.invitation_url || `https://rfp.company.com/invitation/${inv.invitation_id}`,
      company_name: inv.company_name || "Vendor Company"
    }))
    
    await vendorInvitationService.sendInvitations(rfpId.value, invitationObjects, rfpData)
    showToast("Invitations Resent", `Resent ${invitationIds.length} invitation(s).`)
    await loadInvitations()
  } catch (error) {
    console.error('Error resending invitations:', error)
    showErrorToast("Failed to resend invitations")
  } finally {
    loading.value = false
  }
}

const exportInvitations = async () => {
  try {
    const csvContent = generateInvitationsCSV(invitedVendors.value)
    const blob = new Blob([csvContent], { type: 'text/csv' })
    const url = window.URL.createObjectURL(blob)
    const a = document.createElement('a')
    a.href = url
    a.download = `rfp-invitations-${new Date().toISOString().split('T')[0]}.csv`
    a.click()
    window.URL.revokeObjectURL(url)
    showToast("Export Complete", "Invitation list exported successfully.")
  } catch (error) {
    console.error('Error exporting invitations:', error)
    showErrorToast("Failed to export invitations")
  }
}

